.hamburger {
  padding: 10px 10px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  margin-right: -10px;
  overflow: visible;
  &:hover {
    .hamburger-inner {
      background-color: $colorRed;
      &::before, &::after {
        background-color: $colorRed; } } } }

@include r($desktop) {
  .hamburger {
    margin-right: -10px; } }

.hamburger-box {
  width: 29px;
  height: 24px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
  width: 29px;
  height: 5px;
  background-color: #000;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
  &::before, &::after {
    width: 29px;
    height: 5px;
    background-color: #000;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  &::before, &::after {
    content: "";
    display: block; }
  &::before {
    top: -10px; }
  &::after {
    bottom: -10px; } }

.hamburger--spin {
  .hamburger-inner {
    transition: transform .3s cubic-bezier(0.55, 0.055, 0.675, 0.19), background-color .25s ease;
    &::before {
      transition: top 0.1s 0.34s ease-in, opacity 0.1s ease-in, background-color .25s ease; }
    &::after {
      transition: bottom 0.1s 0.34s ease-in, transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19), background-color .25s ease; } }
  &.is-active .hamburger-inner {
    transform: rotate(225deg);
    transition: transform .3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1), background-color .25s ease;
    &::before {
      top: 0;
      opacity: 0;
      transition: top 0.1s ease-out, opacity 0.1s 0.14s ease-out, background-color .25s ease; }
    &::after {
      bottom: 0;
      transform: rotate(-90deg);
      transition: bottom 0.1s ease-out, transform 0.3s 0.14s cubic-bezier(0.215, 0.61, 0.355, 1), background-color .25s ease; } } }
