/* mobile-navi */
.mobile-navi {
  display: none;
  flex-direction: column;
  overflow-y: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 100vh;
  z-index: 9;
  border-top: 1px solid #CCCCCC;
  opacity: 0;
  pointer-events: none;
}

.mobile-navi::before, .mobile-navi::after {
  display: inline-block;
  content: ' ';
  background: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  height: calc(100vh + 100px);
  width: 0vw;
  pointer-events: none;
  transition: all .25s ease-out;
}

.mobile-navi::before {
  left: 50%;
}

.mobile-navi::after {
  right: 50%;
}

.mobile-navi.is-active {
  opacity: 1;
  pointer-events: all;
}

.mobile-navi.is-active::before {
  left: 0;
  width: 50vw;
}

.mobile-navi.is-active::after {
  right: 0;
  width: 50vw;
}

.mobile-navi.is-active .mobile-navi__menu {
  opacity: 1;
}

.mobile-navi__menu {
  position: relative;
  z-index: 2;
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  list-style: none;
  padding: 150px 20px 50px 20px;
  margin: 0 auto;
  width: 100%;
  max-width: 300px;
  opacity: 0;
  transition: all .3s .5s ease;
}

.mobile-navi__menu > li {
  position: relative;
  flex: 1 0 auto;
  display: block;
  width: 100%;
  font-family: 'HistoryPro-Two';
  font-size: 18px;
  padding-right: 30px;
  cursor: pointer;
}

.mobile-navi__menu > li::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: 25px;
  right: 0;
  background: svg-load("../icons/menu-drop.svg") no-repeat center;
  size: 16px 10px;
  transition: all 0.25s ease-in-out;
}

.mobile-navi__menu > li > a {
  display: inline-block;
  padding: 20px 0;
}

.mobile-navi__menu > li > ul {
  display: none;
  position: relative;
  list-style: none;
  padding: 5px 0 5px 15px;
}

.mobile-navi__menu > li > ul::before {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: 15px;
  left: 0px;
  bottom: 15px;
  background: #000000;
  width: 1px;
