.hero {
  position: relative;
  padding-top: 115px;
  margin-bottom: 10px;
}

.hero::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  width: 75%;
  background: #F7F7F7;
}

.hero > .container {
  position: absolute;
  bottom: 0vh;
  left: 0;
  min-height: calc(500px + 8vh + 8px);
  height: calc(100vh - 115px);
  max-height: calc(800px + 115px - 30px);
}

.hero .scrolldown {
  position: absolute;
  bottom: 0;
  left: 37px;
  z-index: 3;
  cursor: pointer;
}

.hero .scrolldown .vert {
  fill: #212526;
}

.hero .scrolldown .oval {
  fill: #212526;
  stroke: #212526;
}

.hero .scrolldown svg .oval {
  transform: translate3d(0, 0px, 0);
  animation: scrollDown 2s ease infinite;
}

.hero--inner {
  margin-bottom: 30px;
}

.hero--inner::after {
  display: none;
}

.hero--inner > .container {
  min-height: 500px;
  height: calc(70vh - 115px);
  max-height: 800px;
}

.hero--inner .scrolldown {
  bottom: 4vh;
}

.hero--inner .scrolldown .vert {
  fill: white;
}

.hero--inner .scrolldown .oval {
  fill: white;
  stroke: white;
}

@keyframes scrollDown {
  0% {
    transform: translate3d(0, 0px, 0);
  }
  25% {
    transform: translate3d(0, -10px, 0);
  }
  50% {
    transform: translate3d(0, 22px, 0);
  }
  100% {
    transform: translate3d(0, 0px, 0);
  }
}

.hero-slider {
  position: relative;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
}

