.photo-slide {
  flex: 0 1 50%;
  display: flex !important;
  flex-direction: column;
  height: auto !important;
  position: relative;
  padding: 0 15px 40px 15px;
  width: 100%;
}

.photo-slide::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: 24px;
  left: 39px;
  right: 0;
  bottom: 0;
  width: calc(100% - 39px);
  background: #F7F7F7;
  z-index: 1;
}

.photo-slide__content {
  position: relative;
  z-index: 2;
}

.photo-slide__image {
  position: relative;
  display: block;
  transition: all 0.25s ease-in-out;
}

.photo-slide__image::after {
  display: none;
  content: ' ';
  position: absolute;
  top: 24px;
  left: -24px;
  right: 24px;
  bottom: -24px;
  background: #F7F7F7;
  z-index: 1;
}

.photo-slide__image img {
  position: relative;
  z-index: 2;
  max-width: 100%;
}

.photo-slide__image:hover {
  opacity: .7;
}

.photo-slide--left .photo-slide__name {
  padding-left: 35px;
}

.photo-slide--right::after {
  left: 0;
  right: 39px;
}

.photo-slide--right .photo-slide__name {
  padding-right: 35px;
}

.photo-slide--right .photo-slide__image::after {
  left: 24px;
  right: -24px;
}

