  .contact-form__top {
    flex-direction: column;
    margin-bottom: 0;
  }
  .contact-form__top .ui-group {
    flex: 0 0 auto;
    width: 100%;
    padding: 0;
    margin-bottom: 15px;
  }
  .contact-form__cta .btn {
    width: 100%;
    max-width: 320px;
  }
}

/* sidebar */
.sidebar {
  flex: 0 1 33.3%;
  min-width: 0;
  min-height: 1px;
  padding-right: 15px;
}

.sidebar__inner {
  padding: 30px 8%;
  background: #F7F7F7;
}

.sidebar__mobile-control {
  display: none;
  justify-content: space-between;
  padding-bottom: 15px;
  border-bottom: 1px solid #000000;
  cursor: pointer;
}

.sidebar__mobile-control > span {
  margin-top: 5px;
  line-height: 1.26;
  font-family: 'HistoryPro-Two';
  font-size: 14px;
}

.sidebar__mobile-control-icon {
  position: relative;
  cursor: pointer;
  size: 24px;
}

.sidebar__mobile-control-icon span {
  position: absolute;
  top: 10px;
  left: 0;
  display: block;
  width: 24px;
  height: 4px;
  background: #000000;
  transition: all 0.25s ease-in-out;
}

.sidebar__mobile-control-icon span:last-child {
  transform-origin: center center;
  transform: rotate(90deg);
}

.sidebar__mobile-control:hover .sidebar__mobile-control-icon span {
  background-color: #FF2934;
}

.sidebar__mobile-control.is-opened .sidebar__mobile-control-icon span {
  background-color: #FF2934;
  transform: rotate(180deg);
}

.sidebar__mobile-control.is-opened .sidebar__mobile-control-icon span:last-child {
  opacity: 0;
}

.sidebar__category {
  margin-right: 30px;
}

.sidebar__category-top {
  display: block;
  position: relative;
  font-family: 'HistoryPro-Two';
  cursor: pointer;
  padding: 15px 0;
  border-bottom: 1px solid #CCCCCC;
}

.sidebar__category-top-icon {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: calc(50% + 10px);
  transform: translateY(-50%);
  right: -30px;
  background: svg-load("../icons/menu-drop.svg") no-repeat center;
  size: 16px 10px;
  transition: all 0.25s ease-in-out;
}

.sidebar__category-top:hover .sidebar__category-top-icon {
  background-image: svg-load("../icons/menu-drop-red.svg");
}

.sidebar__category-top.is-opened {
  border-color: #212526;
}

.sidebar__category-top.is-opened .sidebar__category-top-icon {
  background-image: svg-load("../icons/menu-drop-red.svg");
  transform: translateY(-50%) rotate(180deg);
}

.sidebar__category-num {
  font-size: 12px;
}

.sidebar__category-name {
  margin-top: 5px;
  line-height: 1.26;
  font-family: 'HistoryPro-Two';
  font-size: 24px;
}

.sidebar__category-drop {
  display: none;
  font-family: 'HistoryPro-Two';
  font-size: 14px;
  line-height: 1.28em;
  padding: 10px 0;
  border-bottom: 1px solid #CCCCCC;
}

.sidebar__category-drop.is-opened {
  display: block;
}

.sidebar__category-drop a {
  position: relative;
  display: block;
  padding: 8px 0 8px 30px;
  transition: all 0.25s ease-in-out;
}

.sidebar__category-drop a::after {
  display: inline-block;
  content: ' ';
  width: 16px;
  height: 1px;
  background: #000000;
  position: absolute;
  top: 12px;
  left: 0;
  transition: all 0.25s ease-in-out;
}

.sidebar__category-drop a:hover {
  color: #FF2934;
}

.sidebar__category-drop a:hover::after {
  background: #FF2934;
  transform: translateX(-5px);
}

@media only screen and (max-width: 1150px) {
  .sidebar__category-name {
    font-size: 22px;
  }
}

@media only screen and (min-width: 992px) {
  .sidebar__category {
    display: block !important;
  }
}

@media only screen and (max-width: 992px) {
  .sidebar {
    flex: 0 0 auto;
    width: 100%;
    margin-bottom: 30px;
    margin-right: 0;
  }
  .sidebar__mobile-control {
    display: flex;
  }
  .sidebar__category {
    display: none;
  }
}
