* {
  box-sizing: border-box;
  outline: none; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

body, html {
  background: white;
  color: $fontColor;
  font-family: 'Roboto Slab', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    flex: 1 1 auto;
    border-bottom: 1px solid $borderColor;
    padding-bottom: 40px;
 } }    // padding-top: 115px

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
  &--wide {
    max-width: 1440px; }
  &--full {
    max-width: 100%; }
  &--hero {
    max-width: 100%;
    padding-left: 5%;
    padding-right: 5%; } }

@include r($wide) {
  .container {
    &--hero {
      max-width: 1280px;
      padding-left: 40px;
      padding-right: 40px; } } }

@include r($desktop) {
  .container {
    padding-left: 28px;
    padding-right: 28px;
    &--hero {
      padding-left: 28px;
      padding-right: 28px; } } }

@include r($tablet) {
  .container {
    padding-left: 16px;
    padding-right: 16px; } }


