/* Socials */
.socials {
  &__wrapper {
    display: flex;
    .slick-dots {
      position: absolute;
      bottom: -50px;
      left: 0;
      right: 0; } }

  &__col {
    flex: 0 1 33.3%;
    width: 33.3%;
    padding: 0 12px;
    display: flex;
    flex-direction: column;
    min-width: 0;
    min-height: 1px; }

  &__head {
    text-align: center;
    .social {
      margin-left: auto;
      margin-right: auto; } }

  &__link {
    display: inline-block;
    margin-top: 18px;
    font-size: 16px;
    color: $colorGray;
    @include tr;
    &:hover {
      color: $colorRed; } }

  &__grid {
    display: flex;
    font-size: 0;
    flex-wrap: wrap;
    margin-top: 30px;
    img {
      width: 100%;
      max-width: 100%; }
    a {
      flex: 0 1 50%;
      display: inline-block;
      overflow: hidden;
      @include tr;
      &:hover {
        opacity: .7; } } } }

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  size: 62px;
  border-radius: 50%;
  color: white;
  font-size: 0;
  @include tr;
  .ico {}

  &--facebook {
    background-color: $facebook;
    .ico {
      font-size: 35px; }
    &:hover {
      background-color: $grayBg;
      color: $facebook; } }


  &--youtube {
    background-color: $youtube;
    .ico {
      font-size: 25px; }
    &:hover {
      background-color: $grayBg;
      color: $youtube; } }

  &--instagram {
    background-color: $instagram;
    .ico {
      font-size: 35px; }
    &:hover {
      background-color: $grayBg;
      color: $instagram; } }

  &--vk {
    background-color: $vk;
    .ico {
      font-size: 20px; }
    &:hover {
      background-color: $grayBg;
      color: $vk; } }

  &--small {
    size: 36px;
    .ico {
      transform: scale(.6); } } }

@include r($desktop) {
  .socials {
    margin-bottom: 50px;
    &__col {
      flex: 0 1 50%;
      width: 50%;
      // &:nth-child(3)
 } } }      //   display: none

@include r($tablet) {
  .socials {
    &__col {
      flex: 0 0 auto;
      width: 100%;
      // &:nth-child(2)
 } } }      //   display: none
