.text {
  margin-top: 30px;

  &__wrapper {
    p {
      position: relative;
      font-size: 16px;
      line-height: 1.5;
      color: $colorGray;
      font-family: 'Roboto Slab', sans-serif;

      &:first-child {
        padding-left: 85px; }

      .first-letter {
        display: inline-block;
        position: absolute;
        left: -5px;
        top: 0;
        font-size: 84px;
        line-height: .9;
        text-transform: uppercase;
        color: $fontColor; } } } }


.benefits-list {
  display: flex;
  flex-wrap: wrap;
  &--iwant {
    text-align: center; }
  &__col {
    min-width: 0;
    min-height: 1px;
    flex: 0 1 33.3%;
    width: 33.3%;
    padding: 0 15px;
    margin-bottom: 15px;
    @include tr;
    p {
      font-size: 16px;
      line-height: 1.5;
      color: $colorGray; } }
  &__title {
    margin-top: 15px;
    @include history;
    font-size: 16px;
    letter-spacing: 0.67px; }
  &__modal-mobile {
    display: none;
    position: relative;
    border: 1px solid $colorRed;
    border-right: 0px;
    border-left: 0;
    margin: 30px 0 60px 0;
    padding: 30px 0 30px 20px;
    text-align: left;
    color: $colorGray;
    p {
      margin: 0;
      & + p {
        margin-top: 1em; } }
    &::after {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: 35px;
      bottom: 35px;
      left: 0;
      width: 1px;
      background: $colorRed; } }
  &__modal-hide {
    position: absolute;
    bottom: -40px;
    right: 0;
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: $colorRed;
    @include history;
    @include tr;
    .ico {
      font-size: 15px; }
    &:hover {
      color: $fontColor; }
    span {
      display: inline-block;
      font-size: 12px;
      margin-left: 8px; } } }


@include r($desktop) {
  .benefits-list {
    &__col {
      flex-basis: 50%;
      width: 50%;
      &:nth-child(n+5) {
        display: none; }
      &.is-active {
        .benefits-list__modal-mobile {
          display: block; }
        flex-basis: 100%;
        width: 100%; } } } }

@include r($tablet) {
  .benefits-list {
    flex-direction: column;
    flex-wrap: nowrap;
    &__col {
      flex: 0 0 auto;
      width: 100%;
      &.is-active {
        flex-basis: auto; } } } }


/* Content */
.text-title {
  position: relative;
  font-size: 46px;
  @include history;
  padding-bottom: 90px;
  &::after {
    display: inline-block;
    content: ' ';
    position: absolute;
    bottom: 35px;
    left: 0;
    width: 60px;
    height: 1px;
    background: $fontColor; } }

@include r($wide) {
  .text-title {
    padding-bottom: 70px;
    &::after {
      bottom: 30px; } } }


@include r($tablet) {
  .text-title {
    font-size: 24px;
    padding-bottom: 40px;
    &::after {
      bottom: 20px; } } }



.price-row {
  display: flex;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #EDEBEB;
  &__name, &__price {
    font-size: 18px;
    @include history; }
  &__name {}

  &__price {
    margin-left: auto;
    margin-right: 30px; }
  &__cta {
    flex: 0 0 auto; } }

@include r($tablet) {
  .price-row {
    padding: 15px 0;
    flex-direction: column;
    align-items: stretch;
    &__price {
      margin-left: 0;
      margin-right: auto;
      margin: 15px 0; }
    &__cta {
      .btn {
        width: 100%; } } } }
